import logo from './logo.svg';
import './App.css';
import "bootstrap/dist/css/bootstrap.css";
import { Navbar, Nav, Dropdown, Tabs, Table, Tab } from "react-bootstrap";
import {Component} from "react";


const Requestst = {
    getStudents() {
        return fetch("http://localhost:8080/student/").then(res => res.json());
    }
};

const Requestat = {
    getAt() {
        return fetch("http://localhost:8080/plan/").then(res => res.json());
    }
};
const Requestj = {
    getJ() {
        return fetch("http://localhost:8080/journal/").then(res => res.json());
    }
};

class App extends Component {
    constructor() {
        super();
        this.state = {
            activeGroup: 0,
            students: null,
            plan: null,
            journal: null
        };
    }
    componentDidMount() {
        Requestst.getStudents().then((students) => {
            this.setState({students: students});
        });
        Requestat.getAt().then((plan) => {
            this.setState({plan: plan});
        });
        Requestj.getJ().then((journal) => {
            this.setState({journal: journal});
        });
    }
    render() {
        if(this.state.activeGroup == 0) {
            return (
                <div className="App">
                    {this.state.students ? <StTable
                        students={this.state.students}/> : null}
                    <button onClick={() => {
                        this.setState({activeGroup: 0});
                    }}>Студенты</button>
                    <button onClick={() => {
                        this.setState({activeGroup: 1});
                    }}>Аттестация</button>
                    <button onClick={() => {
                        this.setState({activeGroup: 2});
                    }}>Журнал</button>
                    <button onClick={() => {
                        this.setState({activeGroup: 3});
                    }}>Пересдачи по предмету</button>
                </div>
            );
        }
        if(this.state.activeGroup == 1){
            return (
                <div className="App">
                    {this.state.plan ? <ExamTable
                        plan={this.state.plan}/> : null}
                    <button onClick={() => {
                        this.setState({activeGroup: 0});
                    }}>Студенты</button>
                    <button onClick={() => {
                        this.setState({activeGroup: 1});
                    }}>Аттестация</button>
                    <button onClick={() => {
                        this.setState({activeGroup: 2});
                    }}>Журнал</button>
                    <button onClick={() => {
                        this.setState({activeGroup: 3});
                    }}>Пересдачи по предмету</button>
                </div>
            );
        }
        if(this.state.activeGroup == 2){
            return (
                <div className="App">
                    {this.state.journal ? <JTable
                        journal={this.state.journal}/> : null}
                    <button onClick={() => {
                        this.setState({activeGroup: 0});
                    }}>Студенты</button>
                    <button onClick={() => {
                        this.setState({activeGroup: 1});
                    }}>Аттестация</button>
                    <button onClick={() => {
                        this.setState({activeGroup: 2});
                    }}>Журнал</button>
                    <button onClick={() => {
                        this.setState({activeGroup: 3});
                    }}>Пересдачи по предмету</button>
                </div>
            );
        }
        if(this.state.activeGroup == 3){
            return (
                <div className="App">
                    {this.state.journal ? <CTable
                        journal={this.state.journal}/> : null}
                    <button onClick={() => {
                        this.setState({activeGroup: 0});
                    }}>Студенты</button>
                    <button onClick={() => {
                        this.setState({activeGroup: 1});
                    }}>Аттестация</button>
                    <button onClick={() => {
                        this.setState({activeGroup: 2});
                    }}>Журнал</button>
                    <button onClick={() => {
                        this.setState({activeGroup: 3});
                    }}>Пересдачи по предмету</button>
                </div>
            );
        }
    }
}

class StTable extends Component {
    render() {
        return <Table bordered>
            <thead>
            <tr>
                <th>#</th>
                <th>ФИО</th>
                <th>ID Группы</th>
            </tr>
            </thead>
            <tbody>
            {this.props.students.map((student, index) => {
                return <tr>
                    <td>{index + 1} </td>
                    <td>{student.surname} {student.name} {student.second_name}</td>
                    <td>{student.study_group_id}</td>
                </tr>
            })}
            </tbody>
        </Table>
    }
}

class ExamTable extends Component {
    render() {
        return <Table bordered>
            <thead>
            <tr>
                <th>#</th>
                <th>ID Предмета</th>
                <th>ID Типа экзамена</th>
            </tr>
            </thead>
            <tbody>
            {this.props.plan.map((plan, index) => {
                return <tr>
                    <td>{index + 1} </td>
                    <td>{plan.subject_id} </td>
                    <td>{plan.exam_type_id}</td>
                </tr>
            })}
            </tbody>
        </Table>
    }
}

class JTable extends Component {
    render() {
        return <Table bordered>
            <thead>
            <tr>
                <th>ID студента</th>
                <th>ID Предмета</th>
                <th>ID Оценки</th>
            </tr>
            </thead>
            <tbody>
            {this.props.journal.map((journal, index) => {
                return <tr>
                    <td>{journal.student_id} </td>
                    <td>{journal.study_plan_id}</td>
                    <td style={{color: journal.mark_id!="4" ? 'black' : 'red' }}>{journal.mark_id}</td>
                </tr>
            })}
            </tbody>
        </Table>
    }
}

class CTable extends Component {
    render() {
        return <Table bordered>
            <thead>
            <tr>
                <th>ID студента</th>
                <th>ID Предмета</th>
                <th>Количество пересдач</th>
            </tr>
            </thead>
            <tbody>
            {this.props.journal.map((journal, index) => {
                return <tr>
                    <td>{journal.student_id} </td>
                    <td>{journal.study_plan_id}</td>
                    <td>{journal.count}</td>
                </tr>
            })}
            </tbody>
        </Table>
    }
}
export default App;
